import { Component } from '@angular/core';
import { first } from 'rxjs/operators';


import { AccountService } from './_services';
import { User } from './_models';
import {ConfigService} from './app.init';
import { ConstantsService } from './common/services/constants.service';


@Component({ selector: 'app', templateUrl: 'app.component.html' })
export class AppComponent {
    user: User;


    constructor(private accountService: AccountService, configService: ConfigService, private constantService: ConstantsService) {
      this.constantService.setUrl(configService.config["apiUrl"]);
    }

    ngOnInit() {
      this.accountService.getUser().pipe(first()).subscribe(x => {
      this.accountService.user.subscribe(x => {
        this.user = x
       });
    })
    }

    logout() {
        this.accountService.logout();
    }
}
