import { Injectable } from '@angular/core';
import { Component } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import {ConfigService} from '../app.init';

import { environment } from '@environments/environment';
import { User } from '@app/_models';
import { Config } from '@app/_models';
import { ConstantsService } from '../common/services/constants.service';



@Injectable({ providedIn: 'root' })

export class AccountService {
    private userSubject: BehaviorSubject<User> = new BehaviorSubject(null);
    user = this.userSubject.asObservable();
    apiUrl: string;;

    constructor(
        private router: Router,
        private http: HttpClient,
        configService: ConfigService,
        private constantService: ConstantsService
    ) {
      this.constantService.appUrl.subscribe(url => {
            this.apiUrl = url
            console.log('apiUrl', this.apiUrl);
    });
  }

    public get userValue(): User {
        return this.userSubject.value;
    }

    updatedDataSelection(user){
      this.userSubject.next(user);
  }

    login(user: User) {
        var data = {}
        data["user"] = user
        return this.http.post<User>(`${this.apiUrl}/users/login`, data).pipe(map(userDetail => {
          // this.userSubject.next(userDetail["user"]);
          this.updatedDataSelection(userDetail["user"])
          localStorage.setItem("authToken", userDetail["token"])
        }));
                // store user details and jwt token in local storage to keep user logged in between page refreshes
                // localStorage.setItem('user', JSON.stringify(user));
    }

    logout() {
        // remove user from local storage and set current user to null
        localStorage.removeItem('authToken');
        this.userSubject.next(null);
        this.router.navigate(['/account/login']);
    }

    register(user: User) {
      var data = {}
      data["user"] = user
        return this.http.post(`${this.apiUrl}/users/register`, data);
    }

    getAll() {
        return this.http.get<User[]>(`${this.apiUrl}/users/get_all_users`);
    }

    getById(id: string) {
        return this.http.get<User>(`${this.apiUrl}/users/${id}/user_by_id`);
    }

    getUser(){
        return this.http.get<User>(`${this.apiUrl}/users/get_user_profile`).pipe(map(userDetail => {
          // this.userSubject.next(userDetail["user"]);
          this.updatedDataSelection(userDetail["user"])
        }));
    }

    update(id, params) {
      var data = {}
      data["user"] = params
        return this.http.put(`${this.apiUrl}/users/${id}/update_user_by_id`, data)
            .pipe(map(user => {
                return user;
            }));
    }

    update_image(id,params){
      return this.http.post(`${this.apiUrl}/users/${id}/update_image_by_id`, params)
          .pipe(map(user => {
              return user;
          }));
    }

    delete(id: string) {
        return this.http.get(`${this.apiUrl}/users/${id}/delete_user`)
            .pipe(map(x => {
                // auto logout if the logged in user deleted their own record
                return x;
            }));
    }
}
